@import "semantic/semantic.min.css";
@import "daterangepicker.css";
@import "menu.css";
html {
    body {
        .logo {
            width: 200px;
            margin-bottom: 20px;
        }
        .language_bar_chooser {
            background-color: #d0d0d0;
            border-radius: 100px;
            padding: 3px 8px 3px 15px;
            display: inline-block;
        }
        
        .field {
            label {
                    font-size: 1.1em !important;
            }
        }
        
        table.rate {
            thead,
            tr,
            td {
                text-align: center;
            }
            td,
            th {
                padding: 0px 5px 18px;
            }
            td.number {
                max-width: 70px;
            }
            td.title {
                font-weight: bold;
            }
            td.interest {
                padding: 2px;
            }
        }
    }
}

.immigration {
    .preferences {
        padding: 0 20px;
        text-align: center;
        .field .table {
            max-width: 900px;
            margin: 0 auto;
        }
    }
    .button.huge {
        margin-bottom: 30px;
    }
    .ui.green {
        border-top: 2px solid #21BA45;
        margin: 25px auto !important;
        display: block !important;
    }
}

.center-text {
    text-align: center;
}

.center {
    text-align: center;
    margin: 0 auto;
    display: block;
}

.margin-field {
    margin: 30px 0 1em !important;
}

.full-width {
    width: 100% !important;
    border-bottom: 4px solid !important;
    box-shadow: none !important;
    border-radius: 0 !important;
    .header,
    .meta {
        text-align: center;
    }
}

.no-border {
    border: none !important;
}